import { Mesh, MeshPhongMaterial } from 'three';
import { STLLoader } from 'three/examples/jsm/loaders/STLLoader';
import { ColladaLoader, } from 'three/examples/jsm/loaders/ColladaLoader';
function loadSTL(manager, onComplete, fileURL) {
    new STLLoader(manager).load(fileURL, (result) => {
        const material = new MeshPhongMaterial();
        const mesh = new Mesh(result, material);
        onComplete(mesh);
    }, null, (err) => {
        onComplete(null, err);
    });
}
function loadDAE(manager, onComplete, fileURL) {
    new ColladaLoader(manager).load(fileURL, (result) => {
        onComplete(result.scene);
    }, null, (err) => {
        onComplete(null, err);
    });
}
export { loadSTL, loadDAE };
